
.small {
  position: relative;
  overflow: hidden;
}
canvas {
  height: calc(100vh - 150px) !important;
  width: 100% !important;
}
.about-card {
  background-color: #232127;
  position: absolute;
  display: inline-block;
  text-align: center;
  left: 0;
  right: 0;
  top: 0;
  margin: 0 auto;
}
.about-card h1 {
  color: #fff;
  font-family: Poppins;
  font-size: 25px;
  font-weight: 300;
}
.about-card h2 {
  opacity: 0.5;
  color: #fff;
  font-family: Poppins;
  font-size: 16px;
}
.about-card nav li {
  color: #00dac0;
  cursor: pointer;
  font-family: Raleway;
  font-size: 14px;
  font-weight: 500;
  margin: 25px;
  text-transform: uppercase;
}
.about-card nav li.router-link-active {
  color: #fff;
}


